      h2 2.1 Componentes del presupuesto operativo

    p El presupuesto operativo est compuesto por varios subpresupuestos que, en conjunto, permiten planificar las operaciones y proyectar los recursos necesarios para cumplir los objetivos estratgicos de la empresa. A continuacin, se presentan los principales:

    .row.align-items-center
