          .row(titulo="Presupuesto de ventas")
            .col-12.mb-4.mb-md-0
              p.px-5 Incluye la presupuestacin de volmenes y precios de venta, alineados con la poltica de penetracin de mercado y la estrategia empresarial. Puede elaborarse por lnea de productos o zonas geogrficas. Es el ms complejo debido a los pronsticos que involucra. Se utilizan tcnicas como el mtodo de mnimos cuadrados, regresin lineal y promedios histricos para estimar la participacin en el mercado.

          div(titulo="Presupuesto de produccin")
            p.px-5  Determina las unidades a producir, materias primas a utilizar y comprar, empleados requeridos, valor de la nmina, y costos indirectos de fabricacin. Tambin incluye los inventarios finales segn las polticas empresariales (por ejemplo, justo a tiempo o inventario cero). Estas polticas definen la inversin necesaria en materia prima, productos en proceso y productos terminados.

          div(titulo="Presupuesto de gastos de operacin")
            p.px-5  Proyecta los recursos necesarios para: puesta en marcha, gestin administrativa, distribucin, publicidad y sostenimiento. Se divide en:

